/* eslint-disable @next/next/no-img-element */
import { useState } from 'react';
import { User as UserIcon, Settings as SettingsIcon } from 'lucide-react';
import Settings from '@/components/user-profile/settings/page';
import ProfileDetails from '@/components/user-profile/profile-details/page';

export default function UserProfile() {
  const [activeTab, setActiveTab] = useState('profile');

  const tabs = [
    { id: 'profile', label: 'Profile', icon: <UserIcon /> },
    { id: 'settings', label: 'Settings', icon: <SettingsIcon /> },
  ];

  const renderTabContent = () => {
    switch (activeTab) {
      case 'settings':
        return <Settings />;
      default:
        return <ProfileDetails />;
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      {/* Header (already implemented in your example) */}
      <div className="flex items-center justify-between p-4 bg-white shadow-md">
        <div className="text-xl font-semibold">User Profile</div>
        <div className="flex items-center space-x-4">
          {/* User Profile Header */}
          <div className="flex items-center space-x-4 bg-white p-1 rounded-lg shadow hover:shadow-xl transition-shadow duration-300">
            <div className="relative">
              {/* User Avatar */}
              <img
                src="/profile-pic.jpg"
                alt="Admin Profile Picture"
                className="h-12 w-12 rounded-full border-2 border-blue-500 shadow-md hover:shadow-lg transition-all duration-300"
              />
              <div className="absolute -bottom-1 -right-1 bg-white p-1 rounded-full border border-gray-200 shadow-md">
                <UserIcon className="h-4 w-4 text-blue-500" />
              </div>
            </div>
            <div className="text-sm">
              <span className="block font-semibold text-gray-800 text-lg hover:text-blue-500 transition-all duration-200 cursor-pointer">
                Admin
              </span>
              <p className="text-gray-500 text-sm hover:text-gray-700 transition-colors duration-200">
                admin@example.com
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content Area */}
      <main className="flex-grow container mx-auto px-6 mt-6">
        {/* Tab Navigation */}
        <div className="flex justify-start space-x-6 mb-8 gap-4">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              className={`flex items-center space-x-2 px-6 py-3 text-sm font-medium rounded-lg transition-all duration-300 ${
                activeTab === tab.id
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-blue-500 hover:text-white'
              }`}
              onClick={() => setActiveTab(tab.id)}
            >
              {tab.icon}
              <span className="hidden sm:inline">{tab.label}</span>
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="rounded-lg p-6 sm:p-8 transition-all duration-300">
          {renderTabContent()}
        </div>
      </main>
    </div>
  );
}
